import React from "react";
import wrapper from "../../../commons/WebComponentWrapper";
import Snackbar from "@mui/material/Snackbar";

const dispatchedEvents = [];

const SrsMuiSnackbar = function (props, ref) {
  return (
    <Snackbar
      open={props.open}
      autoHideDuration={props.autoHideDuration}
      onClose={props.onClose}
      message={props.message}
      action={props.action}
      TransitionComponent={props.TransitionComponent}
      anchorOrigin={props.anchorOrigin}
      ContentProps={props.ContentProps}
      theme={props.muiTheme}
    />
  );
};

const styles = `
  /* Custom styles for the MuiSnackbar component */
`;

const props = [
  {
    name: "open",
    defaultValue: false,
    type: "boolean",
  },
  {
    name: "autoHideDuration",
    defaultValue: null,
    type: "number",
  },
  {
    name: "onClose",
    defaultValue: null,
    type: "function",
  },
  {
    name: "message",
    defaultValue: "",
    type: "string",
  },
  {
    name: "action",
    defaultValue: null,
    type: "string",
  },
  {
    name: "TransitionComponent",
    defaultValue: null,
    type: "string",
  },
  {
    name: "anchorOrigin",
    defaultValue: { vertical: "bottom", horizontal: "left" },
    type: "object",
  },
  {
    name: "ContentProps",
    defaultValue: null,
    type: "object",
  },
  {
    name: "sx",
    defaultValue: {},
    type: "object"
  }
];

const slots = [];

const WrappedComponent = wrapper(
  React.forwardRef(SrsMuiSnackbar),
  props,
  "mui"
);
export { WrappedComponent, styles, props, dispatchedEvents, slots };
